<template>
    <div class='style_rank_list'>
        <div class="header">
            <div class="back"  @click.stop="back()">
                <img src="../../assets/image/联合42.png" alt="">
            </div>
            <div class="styleRankHead">学习排行榜</div>
        </div>
        <div class='styleRankContainer'>
            <div class='rankContainer_top'>
                <div class='rankContainer_top_left'>本周排行榜</div>
                <div class='rankContainer_top_right'>
                    <div class='personName'>
                        {{personName}}
                    </div>
                    <div class='ranking_information'>
                        <div class='placement'>第{{personPerTimeSort}}名</div>
                        <div class='totalTime' v-if='parseInt(this.personWeekTime/1000/60/60)>=1'>{{parseInt(this.personWeekTime/1000/60/60)}}小时{{parseInt(parseFloat(this.personWeekTime/1000 % 3600)/60)}}分钟</div>
                        <div class='totalTime' v-if='parseInt(this.personWeekTime/1000/60/60)<1'>{{Math.ceil(this.personWeekTime/1000/60)}}分钟</div>
                    </div>
                </div>
            </div>
            <div class='rankContainer_bottom'>
                <div class='champion'><img src="../../assets/image/皇冠@2x.png" alt=""></div>
                <div v-for="item in personList" :key='item.id' class='rankingList'>
                    <div class='rankingListLeft'>
                        <div class='perTimeSort' v-if='item.perTimeSort <= 3' style='color:#FDB714;font-size:0.3rem'>{{item.perTimeSort}}</div>
                        <div class='perTimeSort' v-if='item.perTimeSort > 3' style='color:#666666;font-size:0.26rem'>{{item.perTimeSort}}</div>
                        <div class='personImg'>
                            <img src="../../assets/image/头像.png" alt="" v-if='item.person.head == null || item.person.head =="" '>
                            <img :src='item.person.head' alt="" v-if='!(item.person.head == null || item.person.head =="")'>
                        </div>
                        <div class='personListName' v-if='item.person.id == personId' style='font-weight: bold'>{{item.person.name}}</div>
                        <div class='personListName' v-if='item.person.id != personId'>{{item.person.name}}</div>
                    </div>
                    <div class='rankingListRight' v-if='parseInt(item.weekTime/1000/60/60)>=1'>
                        <b style='font-size:0.3rem;display:inline-block;margin-right:0.02rem;'> {{parseInt(item.weekTime/1000/60/60)}} </b>
                        <span style='font-size:0.2rem;display:inline-block;margin:0 0.02rem 0.01rem 0;'>小时</span>
                        <b style='font-size:0.3rem;display:inline-block;margin-right:0.02rem;'>{{parseInt(parseFloat(item.weekTime/1000 % 3600)/60)}}</b>
                        <span style='font-size:0.2rem;margin-bottom:0.01rem;'>分钟</span>
                    </div>
                    <div class='rankingListRight' v-if='parseInt(item.weekTime/1000/60/60)<1'>
                        <b style='font-size:0.3rem;display:inline-block;margin-right:0.02rem;'>{{Math.ceil(item.weekTime/1000/60)}}</b>
                        <span style='font-size:0.2rem;margin-bottom:0.01rem;'>分钟</span>
                    </div>
                </div>
            </div>
        </div>
        <!-- 加载页面 -->
        <Loading :dialogVisible="verDialogVisible"></Loading>
    </div>
</template>
<script>
import Loading from './../components/loading'
import API from '@/api/api'
export default {
  components: {
    Loading
  },
  data () {
    return {
      dataList: [], // 数据列表
      personName: '', // 个人名称
      personPerTimeSort: '', // 个人排名
      personWeekTime: '', // 个人学习时长
      personId: '', // 个人id
      personList: [], // 排名列表
      verDialogVisible: true // 加载页
    }
  },
  created () {
    this.getFindWeekTime()
  },
  mounted () {

  },
  methods: {
    // 返回
    back () {
      this.$router.back()
    },
    // 获取个人信息
    getFindWeekTime () {
      API.post('findWeekTime', {
      }, 'json', (data, err) => {
        //   console.log(data)
        if (data.success) {
          this.dataList = data.data
          this.personId = this.dataList.one.person.id
          this.personName = this.dataList.one.person.name
          this.personPerTimeSort = this.dataList.one.perTimeSort
          this.personWeekTime = this.dataList.one.weekTime
          if (this.personWeekTime <= 1000 && this.personWeekTime > 0) {
            this.personWeekTime = 1000
          }
          // this.personWeekTimeMinutes = Math.ceil(data.data.one.weekTime/1000/60);
          for (var i = 0; i < data.data.twenty.length; i++) {
            if (this.dataList.twenty[i].weekTime <= 1000 && this.dataList.twenty[i].weekTime > 0) {
              this.dataList.twenty[i].weekTime = 1000
            }
          }
          this.personList = this.dataList.twenty
          this.verDialogVisible = false
        }
      })
    }

  }
}
</script>
<style scoped>
    .style_rank_list{
        width:100%;
        height:100vh;
        background:rgba(255,255,255,1);
    }
    .header{
        width:100%;
        background:#fff;
        font-size:0.38rem;
        font-family: PingFang-SC-Medium;
        position: fixed;
        top:0;
        height: 0.94rem;
        text-align: center;
        border-bottom: 0.012rem solid  rgba(222,230,242,1);
        z-index: 2;
    }
    .back{
        width:0.89rem;
        height:0.94rem;
        position: fixed;
        top:0;
        left:0;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 99;
    }
    .back img{
        width:0.19rem;
        height:0.34rem;
    }
    .styleRankHead{
        line-height: 0.94rem;
        font-size:0.38rem;
        color:#4E4E4E;
    }
    .styleRankContainer{
        margin-top:0.94rem;
        width:100%;
        padding:0 0.24rem;
        height:calc(100vh - 0.94rem);
        overflow: scroll;
        -webkit-overflow-scrolling: touch;
    }
    .styleRankContainer .rankContainer_top{
        height:1.14rem;
        border-bottom:0.012rem solid #f2f2f2;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom:0.08rem;
    }
    .styleRankContainer .rankContainer_top .rankContainer_top_left{
        font-size:0.3rem;
        color:#333333;
        /* display: inline-block;
        line-height:1.14rem; */
    }
    .styleRankContainer .rankContainer_top .rankContainer_top_right{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height:1.14rem;
        padding-bottom:0.2rem;
        align-items: flex-end;
    }
    .styleRankContainer .rankContainer_top .rankContainer_top_right .personName{
        color:#4E4E4E;
        font-size:0.32rem;
        width:2.15rem;
        white-space: nowrap;
        overflow: hidden;
        padding-top:0.2rem;
        text-overflow: ellipsis;
        text-align: right;
        /* height:0.5rem; */
        /* padding-top:0.04rem; */
    }
    .styleRankContainer .rankContainer_top .rankContainer_top_right .ranking_information{
        color:#271717;
        font-size:0.24rem;
        display: flex;
        justify-content: flex-end;
    }
    .styleRankContainer .rankContainer_top .rankContainer_top_right .ranking_information div{
        display: inline-block;
    }
    .styleRankContainer .rankContainer_top .rankContainer_top_right .ranking_information .placement{
        margin-right:0.1rem;
    }
    .rankContainer_bottom{
        padding:0 0.06rem;
        width:100%;
        position: relative;
    }
    .rankingList{
        height:0.68rem;
        margin-top:0.42rem;
        display: flex;
        align-items: center;
        justify-content: space-between
    }
    .rankingListLeft{
        display: flex;
        align-items: center;
    }
    .champion{
        position: absolute;
        top:-0.22rem;
        left:0.83rem;
        width:0.37rem;
        height:0.27rem;
    }
    .champion img{
        width:100%;
        height:100%;
        display: block;
    }
    .perTimeSort{
        width:0.35rem;
        text-align: center;
        display: inline-block;
        margin-right:0.28rem;
        color:#666666
    }
    .personImg{
        display: inline-block;
        width:0.68rem;
        height:0.68rem;
    }
    .personImg img{
        width:100%;
        height:100%;
        display: block;
        border-radius: 50%;
    }
    .personListName{
        margin-left:0.27rem;
        display: inline-block;
        color:#333333;
        font-size:0.3rem;
        width:2.15rem;
        white-space: nowrap;
        overflow-x: hidden;
        text-overflow: ellipsis;
        /* height:0.68rem; */
        padding:0.225rem 0;
    }
    .rankingListRight{
        margin-right:0.24rem;
        text-align: right;
        display: flex;
        /* align-items: flex-end; */
        align-items: center;

    }
</style>



